// 判断是否是内网、外网环境
// 如果是内网环境，重定向到 http
// 如果是外网环境，重定向到 https

// 通过超时来判断是否是内外网
const PING_TIMEOUT = 1000 * 8;

function ping(ip) {
    return new Promise((resolve, reject) => {
        let timer;

        const img = new Image();

        img.onload = function () {
            clearTimeout(timer);
            resolve(true);
        };
        img.onerror = function (error) {
            clearTimeout(timer);

            // 出错了，也算是通了，只不过返回的数据不是图片数据
            reject(error);
        };
        img.src = ip;

        // 超时
        timer = setTimeout(() => {
            console.log('ping超时');
            // 超时就算不通 浏览器默认超时时间8s左右
            reject(false);
        }, PING_TIMEOUT);
    });
}

function switchHref(isInternal) {
    window.localStorage.setItem('isInternalNetwork', isInternal);
    isInternal ? console.log('当前为内网环境') : console.log('当前为外网环境');
    const currentHref = window.location.href;
    const isHttps = currentHref.startsWith('https');

    // 内网，并且是https协议，转为http协议
    if (isInternal && isHttps) {
        console.log('当前是内网环境，并且是https，跳转到http');
        // window.location.href = currentHref.replace('https', 'http');
    }

    // 外网，并且是http协议，转为https协议
    if (!isInternal && !isHttps) {
        console.log('当前是外网环境，并且不是https方式，跳转到https');
        // window.location.href = currentHref.replace('http', 'https');
    }
}

// 多个url，提高准确率
// 一定要只内网可访问 且图片存在
const pingUrls = [
    'http://p.suixingpay.com/favicon.png',
    'http://p.suixingpay.com/static/svg/logo.svg',
    'http://p.suixingpay.com/static/svg/shift.svg',
    'http://p.suixingpay.com/static/svg/space.svg',
];

let isSuccess = false;
let errorCount = 0;
pingUrls.forEach((url) => {
    ping(url)
        // eslint-disable-next-line no-loop-func
        .then(() => {
            // 有一个成功，就算是内网环境
            if (!isSuccess) {
                isSuccess = true;

                // 是内网
                switchHref(true);
            }
        })
        // eslint-disable-next-line no-loop-func
        .catch(() => {
            errorCount++;

            // 所有都失败了，为外网环境
            if (errorCount === pingUrls.length) {
                // 是外网
                switchHref(false);
            }
        });
});
