class Options extends Array {
    getLabel(value) {
        const record = this.find((item) => item.value === value);
        if (record) return record.label;
        return undefined;
    }

    getTag(value) {
        const record = this.find((item) => item.value === value);
        if (record) {
            const { label, color } = record;
            return `[${color + label}]`;
        }
    }

    getRecord(value) {
        return this.find((item) => item.value === value);
    }
}

const arr = new Options(
    {
        value: '00',
        label: '未审核',
        color: 'red',
    },
    {
        value: '01',
        label: '审核通过',
        color: 'green',
    },
    {
        value: '02',
        label: '已驳回',
        color: 'red',
    },
);

for (let item of arr) {
    console.log(item.label);
}

console.log(arr.getLabel('00'));
